<template>
  <li class="song" @click="setCurrentSongId(songItem.id)">
    <div class="left" v-if="index !== undefined">{{ index + 1 }}</div>
    <dl class="center">
      <dt>{{ songItem.name }}</dt>

      <!--  -->
      <dd v-if="songItem.song">
        <span v-if="songItem.song.hMusic && isShowQuality">SQ</span>
        {{ songItem.song.artists[0].name }}--{{ songItem.song.album.name }}
      </dd>
      <!-- SQ -->
      <dd v-else-if="songItem.h">
        <span v-if="songItem.h && isShowQuality">SQ</span>
        {{ songItem.ar[0].name }}--{{ songItem.al.name }}
      </dd>

      <!--  -->
      <dd v-else-if="songItem.l">{{ songItem.ar[0].name }}--{{ songItem.al.name }}</dd>
      <!--  -->
      <dd v-if="songItem.artists">{{ songItem.artists[0].name }}--{{ songItem.album.name }}</dd>
    </dl>
    <div
      @click="isActive = !isActive"
      :class="{ active: isActive, 'heart fa fa-heart-o': haslove }"
    >
    </div>
    <div class="right fa fa-play-circle-o"></div>
  </li>
</template>

<script>
export default {
  props: ["songItem", "isShowQuality", "index"],
  data() {
    return {
      isActive: false,
      haslove: true
    };
  },
  methods: {
    setCurrentSongId(id) {
      // window.console.log(id);
      this.$emit("translate-currentid", {
        songId: id,
        showFullscreen: window.location.hash.match("search") ? true : false
      });
      // $root.currentSongId =songItem.id
    }
  }
};
</script>

<style lang="less">
li.song {
  display: flex;
  &:nth-child(2n) {
    background: #fafafa;
  }
  &:nth-child(2n-1) {
    background: #f5f5f7;
  }
  dl.center {
    flex: 1;
    //  background: #F5F5F7;
    dt {
      line-height: 30px;
      color: #333;
      margin-left: 5px;
      font-size: 14px;
      width: 200px;
      overflow: hidden;
      white-space:nowrap;
      text-overflow: ellipsis;
    }
    dd {
      line-height: 20px;
      font-size: 12px;
      color: #888;
      width: 200px;
      overflow: hidden;
      white-space:nowrap;
      text-overflow: ellipsis;
      margin-left: 5px;
      span {
        display: inline-block;
        font-weight: bold;
        padding: 0 5px;
        transform: scale(0.5);
        color: #fe672e;
        border: 1px solid #fe672e;
      }
    }
  }
  .heart {
    &.active {
      color: #b82525;
      // background: #b82525;
    }
  }
  
  .left,
  .heart,
  .right {
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #333;
  }
}
</style>
