<template>
  <div class="hot">
    <div class="top">
      <!-- <h1 class="inpic"></h1>
      <div class="des" v-if="topsong">
        <span>更新日期：{{ timeCon(topsong.updateTime) }}</span>
      </div> -->
      <!-- <img src="../assets/index_icon_2x.png" alt=""> -->
    </div>

    <!-- 单个热歌榜 -->
    <!-- <SongListItem
      v-for="(item, index) in topsong.tracks"
      :key="index"
      :song-item="item"
      :is-show-quality="true"
      :index="index"
      @translate-currentid="$emit('translate-currentid', $event)"
    ></SongListItem> -->

    <!-- 所有热歌榜 -->

    <ul class="list">
      <template v-if="allsong">
        <HotAllSong
          :all-hot="item"
          v-for="(item, index) in allsong"
          :key="index"
        ></HotAllSong>
      </template>
    </ul>
  </div>
</template>

<script>
// import SongListItem from "@/components/SongListItem.vue";
import HotAllSong from "@/components/HotAllSong.vue";
export default {
  name: "HomeHot",
  components: {
    // SongListItem,
    HotAllSong
  },
  data() {
    return {
      topsong: Object,
      allsong: Object
    };
  },
  methods: {
    // 拿到热歌榜
    getTopSong() {
      let topsong = JSON.parse(window.localStorage.getItem("topsong"));
      if (topsong && Date.now() < topsong.time) {
        this.topsong = topsong.data;
      } else {
        this.axios
          .get("http://musicapi.leanapp.cn/top/list?idx=1")
          .then(response => {
            // window.console.log(response)
            ({ playlist: this.topsong } = response.data);
            window.localStorage.setItem(
              "topsong",
              JSON.stringify({
                time: Date.now() + 10 * 60 * 1000,
                data: this.topsong
              })
            );
          });
      }

      // 没缓存之前
      // this.axios
      //   .get("http://musicapi.leanapp.cn/top/list?idx=1")
      //   .then(response => {
      //     window.console.log(response);
      //     ({playlist: this.topsong } = response.data);
      //   });
    },

    // 拿到所有热歌榜
    getAllHot() {
      this.axios.get("http://musicapi.leanapp.cn/toplist").then(response => {
        window.console.log(response);
        ({ list: this.allsong } = response.data);
      });
    },
    // 时间转换
    timeCon(n) {
      var now = new Date(n);
      var year = now.getFullYear(n);
      var month = now.getMonth(n) + 1;
      var day = now.getDate(n);
      return year + "年" + month + "月" + day + "日";
    }
  },
  created() {
    this.getTopSong();
    this.getAllHot();
  }
};
</script>

<style lang="less">
.hot {
  // position: relative;
  .top {
    height: 140px;
    width: 100%;
    background: url("../assets/mu.jpg") no-repeat center center;
    background-size: cover;
    position: relative;
    // .inpic {
    //   width: 142px;
    //   height: 67px;
    //   background: url("../assets/index_icon_2x.png") no-repeat;
    //   background-position: -20px -22px;
    //   background-size: 147px 89px;
    //   margin-left: 20px;
    // }
    .des {
      margin-top: 10px;
      margin-left: 20px;
      span {
        font-size: 12px;
        color: #fff;
      }
    }
  }
  ul.list {
    margin-top: 20px;
    display: flex;
    li.card {
      text-align: center;
      flex: 1 0 30%;
      &:nth-child(3n + 2) {
        margin: 0 5px;
      }
    }
  }
}
</style>
