<template>
  <li class="card" @click="handlerCardClick(allHot.id)">
    <div class="thumb">
      <img :src="allHot.coverImgUrl" alt="" />
      <span class="update">更新：{{ timeCon(allHot.updateTime) }}</span>
    </div>
    <div class="name">
      <h6>{{ allHot.name }}</h6>
      <!-- <h6>{{ allHot.updateFrequency}}</h6> -->
    </div>
  </li>
</template>

<script>
export default {
  props: ["allHot"],
  methods: {
    handlerCardClick(param) {
      // window.console.log(param)
      this.$router.push({ path: "playlist", query: { id: param } });
    },

    // 时间转换
    timeCon(n) {
      var now = new Date(n);
      var year = now.getFullYear(n);
      var month = now.getMonth(n) + 1;
      var day = now.getDate(n);
      return year + "-" + month + "-" + day;
    }
  }
};
</script>

<style lang="less">
.thumb {
  position: relative;
  img {
    vertical-align: middle;
  }
  .update {
    position: absolute;
    width: 100%;
    top: 0;
    right: 0;
    color: #fff;
    font-size: 12px;
    display: inline-block;
    // text-overflow: ellipsis;
    white-space: nowrap;
    background: rgba(0, 0, 0, 0.3);
    padding:  5px;
  }
}
.name {
  font-size: 14px;
  color: #333;
}
</style>
