<template>
  <div class="player" :class="{ paused: isPaused }" v-if="currentSongDetail">
    <audio :src="currentSongUrl" autoplay></audio>
    <div class="play-bar" @click="isShowFullscreenPlayer = true">
      <img :src="currentSongDetail.al.picUrl" alt="" />
      <h4>{{ currentSongDetail.name }}</h4>
      <span class="progress" @click.stop="pausedAudio">
        <canvas width="36" height="36"></canvas>
      </span>
    </div>

    <!-- 全屏显示 -->
    <template v-if="isShowFullscreenPlayer">
      <div class="box">
        <!-- 背景 -->
        <div
          class="player-fullscreen"
          :style="{ 'background-image': `url(${currentSongDetail.al.picUrl})` }"
        ></div>
        <span
          @click="isShowFullscreenPlayer = false,iflyrics=false"
          
          class="fa fa-compress"
        ></span>
        <!-- 中间圆圈 -->
        <div class="small"  v-if="!iflyrics">
          <img
            :src="currentSongDetail.al.picUrl"
            alt=""
            @click="iflyrics=true"
            :class="{ paused: isPaused }"
          />
          <div class="bg"></div>
          <div class="bar"></div>
          <h5 class="songname">
            {{ currentSongDetail.name }} --{{ currentSongDetail.ar[0].name }}
          </h5>
        </div>
        <!-- 歌词 -->
        <template  v-if="iflyrics">
          <div class="geci" @click="iflyrics=false">
            <ul class="lyricList">
                <li  v-for="(item, index) in lyrics" :key="index">
                  {{ item.text }}
                </li>
            </ul>
          </div>
        </template>

        <!-- 控制器 -->
        <div class="control">
          <div class="prev fa fa-fast-backward "></div>
          <div
            class="play fa fa-play "
            @click.stop="pausedAudio"
            :class="{ 'fa fa-pause': !isPaused }"
          ></div>
          <div class="next fa fa-fast-forward"></div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  props: ["currentSongId", "showFullscreen"],
  data() {
    return {
      currentSongDetail: null,
      isPaused: false,
      currentTime: 0,
      animateId: null,
      // lyric:"",
      lyrics: null,
      iflyrics:false,
      isShowFullscreenPlayer: this.showFullscreen || false
    };
  },
  computed: {
    // isShowFullscreenPlayer(){
    //   return this.showFullscreen || false;
    // },
    currentSongUrl() {
      return (
        "https://music.163.com/song/media/outer/url?id=" +
        this.currentSongId +
        ".mp3"
      );
    }
  },
  methods: {
    // 获取歌曲详情
    getSongDetail() {
      this.axios
        .get("http://music.kele8.cn/song/detail", {
          params: {
            ids: this.currentSongId
          }
        })
        .then(response => {
          // window.console.log(response);
          this.currentSongDetail = response.data.songs[0];
        });
    },

    // 获取歌词
    getSongLyric() {
      this.axios
        .get("http://music.kele8.cn/lyric", {
          params: {
            id: this.currentSongId
          }
        })
        .then(response => {
           window.console.log(response.data.lrc);
          ({ lyric: this.lyrics } = response.data.lrc);

          let arr =[];
          this.lyrics.split("\n").forEach((element,index)=>{
            var t = element.slice(1,10);
            arr[index]={
              time:Number(t.slice(0,2)) * 60 + Number(t.slice(3)),
              text:element.slice(11).trim()
            };
          });
          arr.pop();
          this.lyrics=arr;
        });
    },

    
    

    pausedAudio() {
      let audio = this.$el.querySelector("audio");
      if (this.isPaused) {
        audio.play();
        // this.drawCircleProgress();
      } else {
        audio.pause();
        // window.clearInterval(this.animateId);
      }
      this.isPaused = audio.paused;
    },
    // 绘制播放进度
    drawCircleProgress() {
      let audio = this.$el.querySelector("audio");

      let canvas = this.$el.querySelector("canvas");
      let ctx = canvas.getContext("2d");

      let id = window.setInterval(() => {
        // window.console.log("dd");
        this.currentTime = audio.currentTime * 1000;
        //   首先清除路径
        ctx.clearRect(0, 0, 36, 36);

        ctx.beginPath();
        ctx.arc(18, 18, 17, 0, 2 * Math.PI);
        ctx.closePath();
        ctx.strokeStyle = "#ccc";
        ctx.stroke();

        //   进度
        ctx.beginPath();
        ctx.arc(
          18,
          18,
          17,
          -0.5 * Math.PI,
          2 * Math.PI * (this.currentTime / this.currentSongDetail.dt) -
            0.5 * Math.PI
        );
        //   ctx.closePath();
        ctx.strokeStyle = "red";
        ctx.stroke();
      }, 1000);
      return id;
    },
    
    songScroll(){
      let songBox = document.querySelector(".geci ul");
      let song = this.lyrics;

      let lis =document.querySelectorAll(".geci ul li");
      let currentTime = this.currentTime /1000;

      let currentIndex =0;
      currentIndex=0;
      window.console.log(currentTime);

      // 查找当前歌词索引
      for (let i = 0; i < lyrics.length; i++) {
      if (lyrics[i].time >= currentTime) {
        // 如果当前歌曲时间 小于 某个歌词时间
        currentIndex =i;
        break;
      }
        
      }

       // 设置当前歌词高亮
      lis.forEach((element, index) => {
        // console.log(index, currentIndex)
        if (index == currentIndex) {
          if (index < 1) {
            lis[0].classList.add("active");
          } else if (index == lyrics.length - 1) {
            lis[index].classList.add("active");
          } else {
            lis[index - 1].classList.add("active");
          }
        } else {
          lis[index].classList.remove("active");
        }
      });
    }
  },
  created() {
    //   创建时拿到数据，第一次进来
    this.getSongDetail();
     this.getSongLyric();
  },
  watch: {
    showFullscreen() {
      // this.isShowFullscreenPlayer = this.showFullscreen;
    },
    currentSongId: function() {
      // 每次id发生变化也拿数据
      this.getSongDetail();
      this.getSongLyric();
      this.isShowFullscreenPlayer = this.showFullscreen;
    },

    isPaused() {
      if (this.isPaused) {
        window.clearInterval(window.animateId);
      } else {
        window.clearInterval(window.animateId);
        window.animateId = this.drawCircleProgress();
      }
    },
    currentSongDetail() {
      this.$nextTick(function() {
        window.clearInterval(window.animateId);
        window.animateId = this.drawCircleProgress();
      });
    },
    currentTime(){
      if(this.iflyrics){
        this.songScroll();
      }
    }
  },
  updated() {}
};
</script>

<style lang="less">
.player {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  //   height: 60px;
  audio {
    height: 60px;
  }
  .play-bar {
    display: flex;
    border-top: 1px solid #ccc;
    background: rgba(255, 255, 255, 0.9);
    padding: 7px 15px;
    line-height: 36px;
    img {
      width: 36px;
      height: 36px;
      border-radius: 50%;
      animation: myroll 5s linear infinite;
    }
    @keyframes myroll {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
    h4 {
      flex: 1;
      margin: 0 10px;
      width: 240px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .progress {
      display: inline-block;
      width: 36px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      //   background: skyblue;
    }
  }
  &.paused {
    .play-bar {
      img {
        animation-play-state: paused;
      }
    }
  }
  .box {
    position: relative;
    z-index: 9999;

    .player-fullscreen {
      width: 100vw;
      height: 100vh;
      background-size: cover;
      background-position: center center;
      filter: blur(5px);
      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 640px;
        background: rgba(0, 0, 0, 0.3);
      }
    }
    .small {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      top: 55px;
      left: 50%;
      margin-left: -148px;
      width: 296px;
      height: 296px;
      // border: 1px solid #000;

      img {
        width: 184px;
        height: 184px;
        border-radius: 50%;
        animation: myroll 10s linear infinite;
        z-index: 100;
        &.paused {
          animation-play-state: paused;
        }
      }
      .bg {
        position: absolute;
        top: 0;
        left: 50%;
        width: 100%;
        height: 100%;
        margin-left: -148px;
        background: url("../assets/disc-ip6.png") no-repeat;
        background-size: cover;
      }
      .bar {
        position: absolute;
        top: -58px;
        left: 142px;
        width: 96px;
        height: 137px;
        background: url("../assets/needle-ip6.png") no-repeat;
        background-size: cover;
      }
      h5 {
        position: absolute;
        // padding: 2px 10px;
        text-align: center;
        bottom: -35px;
        color: #fff;
        line-height: 20px;
        // left: 30%;
        font-size: 20px;
      }
    }
    
    .control {
      width: 100%;
      height: 60px;
      position: fixed;
      bottom: 0;
      left: 0;
      background: skyblue;
      display: flex;
      color: #333;
      // line-height: 60px;
      justify-content: space-around;
      align-items: center;
      div {
        font-size: 30px;
      }
    }
  }

  .fa-compress {
    display: inline-block;
    position: absolute;
    top: 5px;
    left: 5px;
    font-size: 35px;
    color: #fff;
    border: 1px solid #c62f2f;
    border-radius: 5px;
    padding: 5px;
  }
}

 .geci {
  width: 100%;
  // overflow-y: scroll;

  position: relative;
  top: 0;
  left: 0;
  
  ul {
    height: 80vh;
    overflow-y: scroll;
    padding: 30vh 0;
  }

  li {
    padding: 0 50px;
    text-align: center;
    line-height: 25px;
    font-size: 14px;

    &.active {
      color: orange;
    }
  }
}
</style>
