<template>
  <div class="search">
    <!-- HomeSearch -->
    <form>
      <label class="search-input">
        <i class="fa fa-search"></i>
        <input
          type="text"
          v-model.trim="searchText"
          @keyup.enter="
            getMultimatch();
            getSearchList();
          "
        />
        <i class="fa fa-close" @click="searchText = null"></i>
      </label>
    </form>

    <!-- 热门搜索 -->
    <template v-if="!searchText">
      <div class="suggest">
        <h2>热门搜索</h2>
        <ul>
          <li
            v-for="(item, index) in hotsearch"
            :key="index"
            @click="
              (searchText = item.first), getMultimatch();
              getSearchList();
            "
          >
            {{ item.first }}
          </li>
        </ul>
      </div>
      <div class="history">历史记录</div>
    </template>

    <template v-if="searchText && !multimatch">
      <div class="title">搜索 "{{ searchText }}"</div>
      <ul class="lists">
        <li
          v-for="(item, index) in suggest"
          :key="index"
          @click="
            (searchText = item.keyword), getMultimatch();
            getSearchList();
          "
        >
          <i class="fa fa-search"></i>{{ item.keyword }}
        </li>
      </ul>
    </template>

    <!-- 回车后返回的数据 -->
    <div class="match" v-if="multimatch">
      <!-- 歌手 -->
      <template v-if="multimatch.artist">
        <div
          class="artist"
          v-for="item in multimatch.artist"
          :key="item.id"
          @click="$router.push('/artist?id=' + item.id)"
        >
          <img :src="item.picUrl" alt="" />
          <h6>歌手：{{ item.name }}</h6>
          <span class="fa fa-chevron-right"></span>
        </div>
      </template>

      <!-- mv -->
      <template v-if="multimatch.mv">
        <div class="mv" v-for="item in multimatch.mv" :key="item.id">
          <img :src="item.cover" alt="" />
          <h6>
            <p>MV：{{ item.name }}</p>
            <p>{{ item.artistName }}</p>
          </h6>
          <span class="fa fa-chevron-right"></span>
        </div>
      </template>

      <!-- 专辑 -->
      <template v-if="multimatch.album">
        <div
          class="album"
          v-for="item in multimatch.album"
          :key="item.id"
          @click="$router.push('/album?id=' + item.id)"
        >
          <img :src="item.picUrl" alt="" />
          <h6>
            <p>专辑：{{ item.name }}</p>
            <p>{{ item.artist.name }}</p>
          </h6>
          <span class="fa fa-chevron-right"></span>
        </div>
      </template>
    </div>

    <ul v-if="searchList">
      <SongListItem
        v-for="(item, index) in searchList"
        :key="item.id"
        :songItem="item"
        :index="index"
        @translate-currentid="$emit('translate-currentid', $event)"
      ></SongListItem>
    </ul>
    <div class="loaded">
      <img class="load" v-if="loading" src="/sample.gif" alt="" />
    </div>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
// import SearchItem from "@/components/SearchItem.vue";
export default {
  name: "HomeSearch",
  // props: ["item"],
  components: {
    SongListItem
  },

  data() {
    return {
      hotsearch: [],
      suggest: [],
      searchText: null,
      loading: false,
      multimatch: null,
      searchList: []
    };
  },
  methods: {
    // 拿到热搜建议
    getHotSearch() {
      let hotsearch = JSON.parse(window.localStorage.getItem("hotsearch"));
      if (hotsearch && Date.now() < hotsearch.time) {
        this.hotsearch = hotsearch.data;
      } else {
        this.axios
          .get("http://musicapi.leanapp.cn/search/hot/detail")
          .then(response => {
            window.console.log(response);
            ({ hots: this.hotsearch } = response.data.result);
            window.localStorage.setItem(
              "hotsearch",
              JSON.stringify({
                time: Date.now() + 10 * 60 * 1000,
                data: this.hotsearch
              })
            );
          });
      }
    },

    // 拿到搜索建议
    getSearchSuggest() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search/suggest", {
          params: {
            keywords: this.searchText,
            type: "mobile"
          }
        })
        .then(response => {
          // window.console.log(response.data);
          ({ allMatch: this.suggest } = response.data.result);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 搜索多重匹配
    getMultimatch() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search/multimatch", {
          params: {
            keywords: this.searchText
          }
        })
        .then(response => {
          // window.console.log(response.data);
          ({ result: this.multimatch } = response.data);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 歌曲列表
    getSearchList() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search", {
          params: {
            keywords: this.searchText,
            limit: 5,
            offset: 0
          }
        })
        .then(response => {
          // window.console.log(response.data);
          ({ songs: this.searchList } = response.data.result);
        })
        .finally(() => {
          this.loading = false;
        });
    }
  },
  created() {
    this.getHotSearch();
  },
  watch: {
    searchText(newValue) {
      if (newValue) {
        this.getSearchSuggest();
      }
    }
  }
};
</script>

<style lang="less">
.search {
  form {
    padding: 15px 10px;
    position: relative;
  }
  .search-input {
    input {
      width: 340px;
      border-radius: 30px;
      background: #ebecec;
      height: 30px;
      border: none;
      outline: none;
      text-indent: 2em;
      font-size: 14px;
      color: #333;
    }
    .fa-search {
      position: absolute;
      top: 22px;
      left: 18px;
      color: #bbb;
    }
    .fa-close {
      position: absolute;
      top: 22px;
      right: 18px;
      color: #bbb;
    }
  }
  .suggest {
    h2 {
      font-size: 12px;
      color: #666;
      line-height: 12px;
      margin: 5px 10px;
    }
    ul {
      margin-left: 10px;
      li {
        display: inline-block;
        font-size: 14px;
        color: #333;
        padding: 2px 14px;
        border: 1px solid #ddd;
        line-height: 30px;
        margin-right: 8px;
        border-radius: 25px;
        margin-bottom: 8px;
      }
    }
  }
  .history {
    font-size: 12px;
    color: #666;
    line-height: 12px;
    margin: 5px 10px;
  }
  .title {
    font-size: 16px;
    color: #507daf;
    line-height: 50px;
    margin-left: 10px;
    margin-right: 10px;
    // border-top:1px solid rgba(0,0,0,.1);
  }
  ul.lists {
    padding: 0 10px;
    li {
      color: #333;
      font-size: 14px;
      line-height: 45px;
      border-top: 1px solid rgba(0, 0, 0, 0.1);
      i {
        display: inline-block;
        padding: 0 5px;
        color: #bbb;
      }
    }
  }
  .loaded {
    width: 100%;
    display: flex;
    justify-content: center;
    .load {
      width: 50px;
      height: 50px;
    }
  }

  .match {
    .artist {
      display: flex;
      align-items: center;
      padding: 8px 10px;
      img {
        width: 50px;
        height: 50px;
        margin-right: 15px;
      }
      h6 {
        flex: 1;
        color: #333;
        font-size: 16px;
      }
      span {
        display: block;
        width: 20px;
        color: #333;
      }
    }
    .mv {
      display: flex;
      align-items: center;
      padding: 8px 10px;
      img {
        width: 90px;
        height: 50px;
        margin-right: 15px;
      }
      h6 {
        flex: 1;
        color: #333;
        font-size: 16px;
        p {
          &:nth-child(1) {
            line-height: 30px;
          }
          &:nth-child(2) {
            line-height: 20px;
            color: #507daf;
          }
        }
      }
      span {
        display: block;
        width: 20px;
        color: #333;
      }
    }
    .album {
      display: flex;
      align-items: center;
      padding: 8px 10px;
      img {
        width: 50px;
        height: 50px;
        margin-right: 15px;
      }
      h6 {
        flex: 1;
        color: #333;
        font-size: 16px;
        p {
          &:nth-child(1) {
            line-height: 30px;
          }
          &:nth-child(2) {
            line-height: 20px;
            color: #507daf;
          }
        }
      }
      span {
        display: block;
        width: 20px;
        color: #333;
      }
    }
  }
}
</style>
