<template>
  <div class="recommend">
    <HomeTitle>推荐歌单</HomeTitle>
    <ul class="list">
      <SongListCard
        v-for="(item, index) in personalized"
        :key="index"
        :item="item"
      ></SongListCard>
    </ul>
    <HomeTitle>最新音乐</HomeTitle>
    <SongListItem
      v-for="(item, index) in newsong"
      :key="index"
      :song-item="item"
      :is-show-quality="true"
      :index="index"
      @translate-currentid="$emit('translate-currentid', $event)"
    ></SongListItem>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";
import SongListCard from "@/components/SongListCard.vue";
import SongListItem from "@/components/SongListItem.vue";
export default {
  name: "HomeRecommend",
  components: {
    HomeTitle,
    SongListCard,
    SongListItem
  },
  data() {
    return {
      personalized: [],
      newsong: []
    };
  },
  methods: {
    // 拿到歌单
    getPersonalized() {
      let personalized = JSON.parse(
        window.localStorage.getItem("personalized")
      );
      if (personalized && Date.now() < personalized.time) {
        this.personalized = personalized.data;
      } else {
        this.axios
          .get("http://musicapi.leanapp.cn/personalized?limit=6")
          .then(response => {
            // window.console.log(response)
            ({ result: this.personalized } = response.data);
            window.localStorage.setItem("personalized",
            JSON.stringify({
              time:Date.now() + 10 * 60 * 1000,
              data:this.personalized
            })
            );
          });
      }
    },
    // 拿到新歌
    getNewsong() {
      let newsong = JSON.parse(
        window.localStorage.getItem("newsong")
      );
      if (newsong && Date.now() < newsong.time) {
        // 从本地拿数据，本地有数据，且时间还没过10分钟
        this.newsong = newsong.data;
      } else {
        // 从服务器拿到数据
        this.axios
          .get("http://musicapi.leanapp.cn/personalized/newsong")
          .then(response => {
            // window.console.log(response)
            ({ result: this.newsong } = response.data);
            // 存储到本地存储，附加信息，过期时间，拿数据之后10分钟之内有效，否则重新拿
            window.localStorage.setItem("newsong",
            JSON.stringify({
              time:Date.now() + 10 * 60 * 1000,
              data:this.newsong
            })
            );
          });
      }
    }
  },
  created() {
    // 组件创建完成，拿数据
    this.getPersonalized();
    this.getNewsong();
  }
};
</script>

<style lang="less">
ul.list {
  display: flex;
  flex-wrap: wrap;
  li.card {
    flex: 1 0 30%;
    &:nth-child(3n + 2) {
      margin: 0 5px;
    }
    cursor: pointer;
    //  margin-bottom: 10px;
  }
}
</style>
